/* Document */

#contain {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 25px;
  color: rgb(56, 56, 56);
}
/* Header */
#header {
  text-align: center;
  position:fixed;
  height: 120px;
  top:0px;
  left:0px;
  width:100%;
  background:rgb(31, 31, 31);
  z-index: 100;
}
#header-title { 
  text-transform: uppercase;
  font-family: verdana;
  letter-spacing: 10px;
  font-size: 60px;
  font-weight: 700;
  color: #f5f5f5;
}
@media only screen and (max-width: 600px) {
  #header-title {
    font-size: 40px;
  }
}
/* Footer */
#footer {
  text-align: center;
  position:fixed;
  font-size: 15px;
  bottom:0px;
  left:0px;
  width:100%;
  color:#CCC;
  background:rgb(68, 67, 87);
  padding:8px;
}
/* NavBar */

#main-photo {
  margin-top: 5%;
  border-radius: 100px;
  width: 80%;
  justify-content: center;
  justify-items: center;
  align-items: center;
  
}
.nav-item {
  font-size: 20px;
  margin-left: 10px;
  margin-right: 10px;
  color: #CCC;
}
.nav-item:hover {
  color: blueviolet;
}
/* Body */
.mainText {
  margin-top: 125px;
  margin-bottom: 75px;
}

.info {
  font-size: 15px;
}
.mikeImage {
  width: 150px;
  border-radius: 20%;
  float: right;
}
.cards {
  height: 150px;
  width: 150px;
  background-color: aqua;
}
.centering {
  justify-content: center;
  /* align-items: center; */
}
.PageHeader {
  text-align: center;
  font-size: 50px;
  font-weight: bold;
  letter-spacing: 10px;
  margin-bottom: 2%;
}
.iconButton {
  background-color: cadetblue;
}
.iconButton :hover{
  background-color: coral;
}
.MuiCardHeader-title {
  font-size: 20px;
  font-weight: bold;
}